@import url(https://fonts.googleapis.com/css?family=Lacquer&display=swap);
.header {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-logo {
  height: 10vh;
  width: auto;
  margin: 1vh;
}

.shop-header a {
  color: rgb(255, 255, 255);
  text-decoration: none;
}
.shop-header a:hover {
  color: rgba(185, 185, 185, 0.411);
}

.shop-header .logo {
  font-size: 4rem;
  padding-left: 1rem;
}

.shop-header .shopping-cart {
  align-self: center;
  font-size: 1.3rem;
  padding-right: 1rem;
  text-align: center;
}

.shop-header .shopping-cart .cart-icon {
  font-size: 2.2rem;
  margin-right: 10px;
}


.home-page{
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(/Pokemon-App/static/media/browse.b7c95918.jpg) center no-repeat / cover;
    width: 30vh;
    height: 30vh;
    border-radius: 10px;
}

.browse-poke-link {
    font-size: 60px;
    font-weight: 500;
    color: #000;
}

.preview-item-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(18, 41, 70);
  background: rgba(255, 255, 255, 0.575);
}
.preview-item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: 7vh;
}
.item-details-images img {
  width: 15vh;
  height: auto;
  color: #000;
  margin: -.6vh;
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
}

.item-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: 2vh;
}

.preview-item-box h4 {
  font-size: 50px;
}

.item-details-stats {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 2vh 0;
}
.item-details-stats .stats-bar{
  height: 2vh;
  width: 25vh;
  border: 1px solid rgb(18, 41, 70);
}
.item-details-stats .stats-bar .stats-bar-progress{
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

@media (max-width: 768px) { 
  .preview-item {
    margin: 0;
  }
  .item-details-images img {
    margin: 0;
  }
}



.item-list {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background: rgba(255, 255, 255, 0.575);
  padding: 0;
}

.show-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 16vh;
  height: 12vh;
  margin: 1.5vh;
  /* border: 1px solid #fff;
  border-radius: 25px; */
}
.show-item img {
  width: 9vh;
  height: 9vh;
}

.show-item span{
  font-size: 15px;
  font-weight: 500;
  color: #000;
}

/* .show-item:hover{
  text-decoration: black;
} */

.pagination {
  cursor: pointer;
  display: flex;
  justify-content: center;
  margin: 1vh 0;
}
body {
    background: url(/Pokemon-App/static/media/bg1.26b5551b.png) no-repeat fixed center / cover;
    color: #fff;
    margin: 0;
    padding: 0;
    font-family: 'Lacquer', sans-serif;
}
